import APIService from "./index";

export const WaybillsAPI = {
    getWaybills: (itemsPerPage) => {
        return APIService.protectedPost("/waybills/getWaybills", {itemsPerPage})
    },

    getMoreWaybills: (lastId, itemsPerPage) => {
        return APIService.protectedPost("/waybills/getMoreWaybills", {lastId, itemsPerPage})
    },

    getExtendedWaybill: (waybillId) => {
        return APIService.protectedPost("/waybills/getExtendedWaybill", {waybillId})
    },

    updateWaybill: (waybillId, comment, products) => {
        return APIService.protectedPost("/waybills/updateWaybill", {waybillId, comment, products})
    },

    deleteWaybill: (waybillId) => {
        return APIService.protectedPost("/waybills/deleteWaybill", {waybillId})
    },
};