export default {
    getCurrentDate() {
        return new Date();
    },

    toCoolDate: (date) => {
        let month = date.getMonth() + 1;
        if (month < 10) {
            month = '0' + month;
        }

        let Date = date.getDate();
        if (Date < 10) {
            Date = '0' + Date;
        }

        return `${Date}.${month}.${date.getFullYear()}`
    },

    unixTimeStampToDate: (unix) => {
        return new Date(unix * 1000);
    },

    isAfter: (d1, d2) => {
        return d1.getTime() > d2.getTime();
    },
}